body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding-right: 0;
  box-sizing: border-box; }

.center {
  display: flex;
  flex-direction: column; }
  .center .btn-move {
    display: flex;
    justify-content: space-around; }
    .center .btn-move .btn {
      width: 80px;
      height: 40px;
      background: #00afdb;
      color: white;
      border: black;
      cursor: pointer;
      margin: 10px 0 10px 0; }

.next span:hover {
  font-size: 18px; }

.back span:hover {
  font-size: 18px; }

.loader {
  display: flex;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: 3em;
  letter-spacing: 1px;
  color: #00afdb; }

.show-beer {
  max-width: 1100px;
  margin: 0 auto; }

.beer-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  padding-left: 0; }

.notFound {
  display: flex;
  justify-content: center;
  margin-bottom: 800px; }

.single-beer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 200px;
  border: 1px solid #00afdb;
  margin: 8px 8px 0 8px;
  box-shadow: 3px 2px 5px 0px rgba(0, 0, 0, 0.43); }

.single-beer-img {
  max-width: 100%;
  height: 150px;
  margin: 10px 0 10px 0;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center; }

.single-beer-name {
  padding: 10px 3px 10px 5px;
  border-top: 1px solid #00afdb; }

.single-beer-desc {
  font-size: 13px;
  padding: 10px 3px 0 5px;
  color: rgba(0, 0, 0, 0.4); }

.single-beer-brew-sheet ul {
  list-style: none;
  padding-left: 5px;
  padding-bottom: 5px;
  padding-top: 12px; }
  .single-beer-brew-sheet ul li {
    padding-left: 26px;
    padding-top: 4px;
    padding-bottom: 10px;
    font-size: 12px; }
  .single-beer-brew-sheet ul #abv {
    background: url(data:image/png;base64,) no-repeat left top;
    background-size: 20px 20px; }
  .single-beer-brew-sheet ul #ibu {
    background: url(data:image/png;base64,) no-repeat left top;
    background-size: 20px 20px; }
  .single-beer-brew-sheet ul #malts {
    background: url(data:image/png;base64,) no-repeat left top;
    background-size: 20px 20px; }
  .single-beer-brew-sheet ul #hops {
    background: url(data:image/png;base64,) no-repeat left top;
    background-size: 20px 20px; }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6); }

.modal-main {
  position: fixed;
  background: white;
  color: black;
  width: 300px;
  height: auto;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 20px 10px 20px 10px; }

h3 {
  padding: 10px 10px 10px 10px; }

p {
  padding: 5px 10px 5px 10px; }

.btn-more {
  border: white;
  background: white;
  cursor: pointer; }

.btn-more:hover {
  background: #00afdb; }

.btn-close {
  margin: 0 auto;
  display: block;
  padding: 5px;
  background: #00afdb;
  border: white;
  color: white; }

.display-block {
  display: block; }

.display-none {
  display: none; }

.logo-wraper {
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(/your-beer-react-app/static/media/headerBackground.db5867b1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }
  .logo-wraper .logo {
    width: 200px;
    height: 200px;
    background-image: url(data:image/jpeg;base64,);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center; }
  .logo-wraper .text-logo {
    font-size: 36px;
    color: white;
    background-color: black; }

.random-beer {
  display: flex;
  justify-content: center;
  margin-top: 25px; }
  .random-beer h2 {
    font-size: 36px;
    color: white;
    background-color: black; }

.search-bar {
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  height: 40px;
  font-size: 22px; }
  .search-bar__text {
    display: flex;
    width: 640px; }
    .search-bar__text .form-control {
      width: calc(100% - 50px);
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      font-size: 22px; }
    .search-bar__text .form-control:focus {
      outline: #00afdb solid 2px;
      font-size: 22px; }

